.search__input {
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    height: 30px;
    padding: 10px 20px;
    border-radius: 99px;
    width: 75vw; /*use 75% of the screen width*/
    margin: 0 auto; /* horizontally center */
    margin-top: 40px;
    max-width: 560px;
}

.search__input > input {
    flex: 1; /* take up majority space */
    padding: 10px 20px;
    font-size: medium;
    border: none;
}  

.search__input > input:focus {
    outline-width: 0;
}

.search__buttons {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    
}

.search__buttons > button {
    margin: 5px; 
    padding: 7px 15px;
    background-color: #f8f8f8; /*light grey?*/
    border: 1px solid white;
    text-transform: inherit; /* revert to lower case */
    color: grey;
    
}

.search__buttons button:hover {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    background-color: #f8f8f8;
    border: 1px solid #c6c6c6;
    color: #222;
}

.search__buttonsHidden {
    display: none !important;
}